import { useEffect, useState } from 'react';
import Article from '../interop/Article';
import Contentful from './Contentful';

const useEditorialArticles = () => {
  const [articles, setArticles] = useState<Article[]>([]);
  const [ready, setReady] = useState<boolean>(false);

  const contenful = new Contentful();


  useEffect(() => {
    contenful.getArticles().then((response: any) => {
      const articles = response.items.map((article: any) => ({
        title: article.fields.title,
        author: article.fields.author,
        link: `/editorial/${article.sys.id}`,
        date: article.sys.createdAt
      }));
      setArticles(articles);
      setReady(true);
    });
  }, []);

  return {
    ready,
    articles
  };
};

export default useEditorialArticles;
