import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import { useLocation, useNavigate } from 'react-router-dom';

const Navigation = () => {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const params = pathname.split('/');
  const activeTab = params[1];

  const handleChange = (_: any, newValue: any) => navigate(`/${newValue}`);

  const handleClick = (e: any) => {
    navigate(`/${e.target.getAttribute('data-value')}`);
  }

  return (
    <Tabs
      value={activeTab}
      onChange={handleChange}
      textColor="secondary"
      indicatorColor="secondary"
      aria-label="secondary tabs example"
    >
      <Tab data-value="editorial" value="editorial" label=" " onClick={handleClick} />
      <Tab data-value="guardian" value="guardian" label="The Guardian" onClick={handleClick} />
      <Tab data-value="reddit-worldnews" value="reddit-worldnews" label="r/worldnews" onClick={handleClick} />
      <Tab data-value="reddit-UkraineConflict" value="reddit-UkraineConflict" label="r/UkraineConflict" onClick={handleClick} />
      <Tab data-value="reddit-wallstreetbets" value="reddit-wallstreetbets" label="r/wallstreetbets" onClick={handleClick}/>
    </Tabs>
  )
};

export default Navigation;
