import { useState } from 'react';
import ArticleExcerpt from './ArticleExcerpt';
import useGuardianArticles from './useGuardianArticles';
import useUaLiveBlog from './useUaLiveBlog';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import { Pagination } from '@mui/material';
import { Box } from '@mui/system';

const Feed = () => {
  const [page, setPage] = useState<number>(1);
  const [blogPage, setBlogPage] = useState<number>(1);
  const { articles, pages } = useGuardianArticles(page);
  const { articles: blogArticles, pages: blogPages } = useUaLiveBlog(blogPage);

  return (
    <Container maxWidth="md">
      <Box sx={{ marginTop: '30px' }}>
        <Typography variant="h4"> :</Typography>
        {blogArticles.map((article: any) => <ArticleExcerpt key={article.webTitle} article={article} />)}
        <Pagination sx={{ marginTop: '20px' }} count={blogPages} color="primary" page={blogPage} onChange={(event: any, value: any) => setBlogPage(value)} />
      </Box>

      <Box sx={{ marginTop: '30px' }}>
        <Typography variant="h4"> :</Typography>
        {articles.map((article: any) => <ArticleExcerpt key={article.webTitle} article={article} />)}
        <Pagination sx={{ marginTop: '20px' }} count={pages} color="primary" page={page} onChange={(event: any, value: any) => setPage(value)} />
      </Box>
    </Container>

  )
}

export default Feed;
