import './index.css';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import GuardianArticle from './guardian/Article';
import Feed from './guardian/Feed';
import Navigation from './components/Navigation';
import RedditFeed from './RedditFeed';
import { Container } from '@mui/material';
import EditorialFeed from './editorial/EditorialFeed';
import EditorialArticle from './editorial/Article';


function App() {
  return (
    <Router>
      <Container maxWidth="md">
        <Navigation />
        <Routes>
          <Route path='/editorial' element={<EditorialFeed />} />
          <Route path='/editorial/*' element={<EditorialArticle />}/>
          <Route path='/guardian/*' element={<GuardianArticle />} />
          <Route path='/guardian' element={<Feed />} />
          <Route path='/reddit-worldnews' element={<RedditFeed subreddit="worldnews" />} />
          <Route path='/reddit-UkraineConflict' element={<RedditFeed subreddit="UkraineConflict" />} />
          <Route path='/reddit-wallstreetbets' element={<RedditFeed subreddit="wallstreetbets" />} />
          <Route path='/' element={<EditorialFeed />} />
        </Routes>
      </Container>
    </Router>
  );
}

export default App;
